import React from 'react';
import './header.scss';

class Menu extends React.Component{
  constructor(props){
    super(props);
    this.state = {};
  }

  mobielMenu = () => {
    var x = document.getElementById("myTopnav");
    if (x.className === "topnav") {
      x.className += " responsive";
    } else {
      x.className = "topnav";
    }
  }
    
  render(){
    return(
        <div className="main-menu-outer">
          <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css"></link>
          <nav className='main-menu region'>
            <div className="topnav" id="myTopnav">
              <a id="house" href="/" className="active"><img alt="" src="/images/house.png" /></a>
              <a href onClick={(e) => {e.preventDefault()}}>News</a>
              <a href onClick={(e) => {e.preventDefault()}}>Contact</a>
              <a href onClick={(e) => {e.preventDefault()}}>About</a>
              <a href="/admin">Login</a>
              <a href onClick={(e) => {e.preventDefault(); this.mobielMenu()}} className="icon" >
                <i className="fa fa-bars"></i>
              </a>
            </div>
          </nav>
        </div>
    );
  }
}

export default Menu;
